import React, { useEffect, useState } from 'react'

import FacebookIcon from '@material-ui/icons/Facebook';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';

// local components
import Fade from "./Fade";
import NavBar from './NavBar'
import Project from './Project'

// local images
import BitProjectImage from '../assets/bitproject.jpg'
import SongsWithFriendsImage from '../assets/songswithfriends.jpg'
import UCDavisHealthImage from '../assets/healthyu.jpg'

export default function Home() {
  const [show, setShow] = useState(false);
  useEffect(() => {
    setShow(show => !show)
  }, [])
  return (
    <>
      <NavBar />
      <header className="home-header">
        <div className="header-text">
          <h1>John's Portfolio</h1>
          <h2>Software Engineer + Web Developer</h2>
          {/* <p>Hi! I'm John, an aspiring software engineer who enjoys learning web development!<br />
          Currently taking part of the Wonsulting 2020 Project
          </p> */}
        {/* <img className="home-img" alt="John Voong" src={Pic} /> */}
        </div>
      </header>
      <main className="home-main">
        <h2>Projects</h2>
        <Fade show={show}>
          <Project
            title="Bit Project Flask Developer"
            imageFirst={false}
            Image={BitProjectImage}
            imageAlt="Bit Project Image"
            summary="Web developer and comprehensive curriculum writer for Bit Project"
            learnMore="/bitproject"
          />
        </Fade>
        <Fade show={show}>
          <Project
            title="UCD Health & MAE ReactJS Developer"
            imageFirst={true}
            Image={UCDavisHealthImage}
            imageAlt="UC Davis Health"
            summary="ReactJS developer for UCD Health Dpt. & Mech. Aerospace Engineers Dpt."
            learnMore="/healthyu"
          />
        </Fade>
        <Fade show={show}>
          <Project
            title="Songs with Friends"
            imageFirst={false}
            Image={SongsWithFriendsImage}
            imageAlt="Songs with Friends image"
            summary="Initially a course project, remade/polished from vanilla HTML/CSS/JS to ReactJS, SCSS, PostgreSQL (in progress)"
            learnMore="/swf"
          />
        </Fade>
      </main>
      <footer>
        <Fade show={show}>
        <a href="https://github.com/jhvoong">
          <GitHubIcon className="link-icon" />
        </a>
        </Fade>
        <a href="https://www.linkedin.com/in/john-voong7/">
          <LinkedInIcon className="link-icon" />
        </a>
        <a href="https://www.facebook.com/john.voong.68">
          <FacebookIcon className="link-icon" />
        </a>
      </footer>
    </>
  )
}